export const get = (url) => {
    return fetch(url, {
        method: 'GET',
        credentials: 'include',
        headers: {
            'Accept': 'application/json, text/plain, */*'
        }
    }).then((res) => {
        // res包含状态码、状态消息
        if (res.status >= 200 && res.status < 300) {
            return res.json();
        }
        else {
            // 状态码是404或500走这里
            const error = new Error('业务处理异常');
            error.res = res;
            return error
        }
    });
}

/*
 *  http协议中，post方法提交数据时，会通过Content-Type和服务端约定数据编码方式
 *  Content-Type有四种:
 * 'application/x-www-form-urlencoded': 使用encodeURIComponent进行编码，提交的数据按照 key1=val1&key2=val2 的方式进行编码
 * 'multipart/form-data': 使用表单上传文件时，必须让 <form> 表单的 enctype 等于 multipart/form-data
 * 'application/json': 使用JSON字符串
 * 'text/xml'
 */
export const post = (url, params) => {
    return fetch(url, {
        method: 'POST',
        credentials: 'include',
        headers: {
            'Accept': 'Application/json, text/plain, */*',
            'Content-Type': 'application/x-www-form-urlencoded'
        },
        body: objToJsonString(params)
    }).then((res) => {
        // res包含状态码、状态消息
        if (res.status >= 200 && res.status < 300) {
            return res.json();
        }
        else {
            // 状态码是404或500走这里
            const error = new Error('业务处理异常');
            error.res = res;
            return error
        }
    });
}

const objToJsonString = (obj) => {
    let result = '';
    let item;
    for (item in obj) {
        if ('object' === typeof obj[item]) {
            result += `&${item}=${encodeURIComponent(JSON.stringify(obj[item]))}`;
        }
        else {
            result += `&${item}=${obj[item]}`;
        }
    }

    if (!!result) {
        result = result.slice(1);
    }

    return result;
}



