import * as React from 'react';
import FeeRecord from './components/FeeRecord';
import styles from './index.scss';

const ICON_HOUSE = require('../../common/resources/detail_house.png');
const TEXT_PAY_SUCCESS = '支付成功';
const TEXT_PAYTYPE = '支付方式';
const TEXT_PAYTIME = '支付时间';
const TEXT_PAYNO = '支付编号';

class FeeDetail extends React.Component {
    static defaultProps = {
        data: {
            number: '120',
            point: '.12',
            payType: '微信支付',
            payTime: '2018-9-12 13:50',
            payNo: 'X123XKDJKF12314',
            address: '凯滨路166号',
            name: '平安大厦',
            data: [
                {
                    year: '2018',
                    month: '1',
                    fee: '120.00',
                    data: {
                        manager: '100.00',
                        sanitation: '15.00',
                        elevator: '5.00'
                    }
                },
                {
                    year: '2018',
                    month: '2',
                    fee: '120.00',
                    data: {
                        manager: '100.00',
                        sanitation: '15.00',
                        elevator: '5.00'
                    }
                },
                {
                    year: '2018',
                    month: '3',
                    fee: '120.00',
                    data: {
                        manager: '100.00',
                        sanitation: '15.00',
                        elevator: '5.00'
                    }
                },
                {
                    year: '2018',
                    month: '4',
                    fee: '120.00',
                    data: {
                        manager: '100.00',
                        sanitation: '15.00',
                        elevator: '5.00'
                    }
                }
            ]
        }
    }

    render() {
        const { data } = this.props;
        return (
            <div className={styles.root}>
                <div className={styles.feeContainer}>
                    <div className={styles.iconContainer}>
                        <img className={styles.icon}
                            src={ICON_HOUSE}
                            alt='' />
                        <span className={styles.label}>物业缴费</span>
                    </div>
                    <div className={styles.money}>
                        <span className={styles.symbol}>
                            ￥
                        </span>
                        <span className={styles.number}>
                            {data.number}
                        </span>
                        <span className={styles.symbol}>
                            {data.point}
                        </span>
                    </div>
                    <div className={styles.tip}>
                        {
                            TEXT_PAY_SUCCESS
                        }
                    </div>
                </div>
                <div className={styles.paymentInfo}>
                    <div className={styles.line}>
                        <span className={styles.type}>
                            {
                                TEXT_PAYTIME
                            }
                        </span>
                        <span className={styles.typeName}>
                            {data.payType}
                        </span>
                    </div>
                    <div className={styles.line}>
                        <span className={styles.type}>
                            {
                                TEXT_PAYTYPE
                            }
                        </span>
                        <span className={styles.typeName}>
                            {
                                data.payTime
                            }
                        </span>
                    </div>
                    <div className={styles.line}>
                        <span className={styles.type}>
                            {
                                TEXT_PAYNO
                            }
                        </span>
                        <span className={styles.typeName}>
                            {
                                data.payNo
                            }
                        </span>
                    </div>
                </div>
                <FeeRecord data={data.data}
                    address={data.address}
                    name={data.name} />
            </div>
        )
    }
}

export default FeeDetail;


